/* Basic Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html, #root {
  height: 100%;
}

.app {
  display: flex;
  height: 100vh;
  background: #f0f0f0; /* light gray background */
  font-family: Arial, sans-serif;
  position: relative;
}

/* Support Gender Medicine Section */
.support-gender-medicine {
  position: absolute;
  top: 10px;
  right: 10px;
  display: flex;
  align-items: center;
  gap: 10px;
}

.support-text {
  font-size: 1rem;
  font-weight: bold;
  color: #333;
}

.like-button {
  background: #ff4081;
  border: none;
  color: white;
  padding: 5px 10px;
  border-radius: 20px;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 5px;
  font-size: 0.9rem;
  transition: transform 0.2s;
}

.like-button:hover {
  transform: scale(1.1);
}

.like-count {
  font-weight: bold;
}

/* Social Sidebar */
.social-sidebar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 60px;
  background: #333;
}

.social-sidebar a {
  color: #fff;
  font-size: 1.5rem;
  margin: 10px 0;
  transition: transform 0.2s;
}

.social-sidebar a:hover {
  transform: scale(1.2);
}

/* Main Content */
.main-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  text-align: center;
  height: 100%;
}

.logo {
  font-size: 4rem;
  font-weight: bold;
  margin-bottom: 20px;
}

.contact-info {
  font-size: 1.2rem;
  color: #333;
}

.contact-info a {
  color: inherit;
  text-decoration: none;
}

.contact-info a:hover {
  text-decoration: underline;
}

.name {
  margin-bottom: 10px;
  font-weight: bold;
}

.email {
  margin-bottom: 10px;
}

.welcome-message {
  margin-top: 10px;
  font-size: 1.2rem;
  font-weight: 600;
}

/* Bottom Social Links */
.social-links-bottom {
  display: flex;
  justify-content: center;
  gap: 15px;
  margin-top: 15px;
}

.social-links-bottom a {
  color: #333;
  font-size: 1.5rem;
  transition: transform 0.2s;
}

.social-links-bottom a:hover {
  transform: scale(1.2);
}

/* CV Button */
.cv-button {
  margin-top: 20px;
  padding: 10px 20px;
  background: #333;
  color: #fff;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.2s;
}

.cv-button:hover {
  background: #555;
}

/* CV Modal */
.cv-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  z-index: 1000;
}

.cv-modal-content {
  background: #fff;
  padding: 30px;
  border-radius: 10px;
  max-width: 600px;
  width: 100%;
  max-height: 80vh;  /* Limits modal height to 80% of viewport */
  overflow-y: auto;  /* Enables scrolling if content is too long */
  text-align: left;
  position: relative;
  line-height: 1.6;
}

/* Spacing for headings and paragraphs inside the modal */
.cv-modal-content h2,
.cv-modal-content h3 {
  margin-top: 20px;
  margin-bottom: 10px;
}

.cv-modal-content p {
  margin-bottom: 15px;
}

.cv-close-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: #ff4081;
  border: none;
  color: #fff;
  padding: 8px 12px;
  font-size: 0.9rem;
  border-radius: 5px;
  cursor: pointer;
  transition: background 0.2s ease, transform 0.2s ease;
}

.cv-close-button:hover {
  background: #e73370;
  transform: scale(1.05);
}

.like-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}


/* Responsive Adjustments */
@media (max-width: 600px) {
  .social-sidebar {
    width: 50px;
  }
  
  .social-sidebar a {
    font-size: 1.2rem;
    margin: 8px 0;
  }
  
  .logo {
    font-size: 3rem;
  }
  
  .contact-info {
    font-size: 1rem;
  }
  
  .welcome-message {
    font-size: 1rem;
  }
  
  .social-links-bottom a {
    font-size: 1.2rem;
  }
  
  .support-text {
    font-size: 0.9rem;
  }
  
  .like-button {
    padding: 4px 8px;
    font-size: 0.8rem;
  }
}
